.modalContent {
    min-width: 350px;
    display: flex;
    flex-direction: column;
  }
  
  .modalContent li {
    display: flex;
    flex-direction: column;
    text-align: left;
    gap: 4px;
    font-size: 16px;
    margin: 1rem 0;
  }
  
  .modalContent input,
  .modalContent select {
    padding: 0.5rem 1rem;
    border-radius: 12px;
    border: 1px solid #dfeaf2;
    background-color: #fff;
    font-size: 15px;
    font-family: sans-serif;
    color: #718ebf;
  }
  
  .button {
    border-radius: 12px;
    background-color: #2d60ff !important;
    color: #fff;
    border: none;
    font-size: 15px;
    padding: 0.5rem 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .button:hover {
    background-color: #254ab8 !important;
  }
  
  .button:active {
    transform: scale(0.98);
  }
  
  .icon {
    font-size: 1.25rem;
    margin: 0 1rem;
  }
  
  .editBtn {
    color: #2d60ff;
  }